import {Component, OnInit} from '@angular/core';
import {Food} from '../../../models/food.model';
import {FoodAdminService} from '../../../services/food-admin.service';
import {Router} from '@angular/router';
import {DataUrl, NgxImageCompressService} from 'ngx-image-compress';
import {DomSanitizer} from '@angular/platform-browser';
import {Location} from '@angular/common';
import {TokenStorageService} from '../../../_servicesAuth/token-storage.service';

@Component({
  selector: 'app-add-food',
  templateUrl: './add-food.component.html',
  styleUrls: ['./add-food.component.scss'],
})
export class AddFoodComponent implements OnInit {
  private selectedFile: File;
// eslint-disable-next-line @typescript-eslint/member-ordering
  public avatarURL: any;
  private imageSource: any;
// eslint-disable-next-line @typescript-eslint/member-ordering
  imgResultAfterResizeMax: DataUrl = '';
  // eslint-disable-next-line @typescript-eslint/member-ordering
  focused: boolean;
  // eslint-disable-next-line @typescript-eslint/member-ordering
  currentUser: any;

  constructor(
    private sanitizer: DomSanitizer,
    private foodService: FoodAdminService,
    private router: Router,
    private imageCompress: NgxImageCompressService,
    private location: Location,
    private token: TokenStorageService,
  ) {
  }

  ngOnInit(): void {
    this.currentUser = this.token.getUser();
  }

// eslint-disable-next-line @typescript-eslint/member-ordering
// @ts-ignore
// eslint-disable-next-line @typescript-eslint/member-ordering
  food: Food = {
    title: '',
    price: null,
    image: '',
    description: '',
    published: true,
    //userid : null
  };

  onFileSelect(event) {
    this.selectedFile = event.target.files[0];
    console.log(this.selectedFile.name);
  }

  changeAvatar(files) {
    if (files.length === 0) {
      return;
    }

    const mimeType = files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      console.log('Only images are supported.');
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(files[0]);
    reader.onload = (_event) => {
      this.avatarURL = reader.result;
      console.log('the data is', this.avatarURL as string); // data 64 bit
    };
  }

  showImage(): void {
    console.log('testok');
    this.imageSource = this.sanitizer.bypassSecurityTrustResourceUrl(`${this.avatarURL as string}`);
    console.log('this is image', this.imageSource);

  }

  uploadAndReturnWithMaxSize() {
    return this.imageCompress.uploadAndGetImageWithMaxSize(1, true).then(
      (result: DataUrl) => {
        this.imgResultAfterResizeMax = result;
        console.log('-->', result);
      },
      (result: string) => {
        console.error(
          'The compression algorithm didn\'t succed! The best size we can do is',
          this.imageCompress.byteCount(result),
          'bytes'
        );
        this.imgResultAfterResizeMax = result;
      }
    );
  }

  saveFood(): void {
    const data = {
      title: this.food.title,
      price: this.food.price,
      image: this.imgResultAfterResizeMax,
      description: this.food.description,
      published: this.food.published,
      //userid: this.currentUser,
    };

    this.foodService.create(data)
      .subscribe(
        response => {
          console.log(response);
                   //this.router.navigate(['client1/admin/listing']);
          window.location.reload();
        },
        error => {
          console.log(error);
        });
    this.location.back();
  }

  onBlur(event: any) {
    const value = event.target.value;

    if (!value) {
      this.focused = false;
    }
  }

}

