;\n  width: 80px;\n}\n.image {\n  border-radius: 70%;\n  height: 50px;\n  width: 200px;\n}\n.title {\n  font-weight: 700;\n  color: #ffb800;\n  font-size: 1.2rem;\n  margin-right: 25%;\n}\nform .forgot-password-link {\n  text-decoration: none;\n  display: inline-block;\n  width: 100%;\n  text-align: right;\n  font-weight: 600;\n  font-size: 14px;\n  color: #4a4a4a;\n}\nform app-button {\n  display: block;\n  margin-top: 3rem;\n}\n.alert-danger {\n  color: red;\n  padding-top: 48px;\n  text-align: center;\n}\n.form-field {\n  position: relative;\n  margin-bottom: 1rem;\n  height: 50px;\n}\n.form-field .label {\n  position: absolute;\n  left: 1rem;\n  top: 1rem;\n  padding: 0 0.25rem;\n  color: #939fab;\n  font-size: 1rem;\n  transition: 0.3s;\n  padding-left: 20px;\n}\n.form-field input {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 100%;\n  width: 100%;\n  font-size: 17px;\n  font-weight: 600;\n  border: 2px solid #e7ebee;\n  border-radius: 10px;\n  outline: none;\n  padding: 1rem;\n  background: none;\n  z-index: 1;\n  color: #171c25;\n}\n.form-field input:focus {\n  border-color: #fa578e;\n}\n.form-field .label-focused {\n  top: -0.4rem;\n  left: 0.8rem;\n  font-size: 14px;\n  font-weight: 600;\n  background-color: #fff;\n  z-index: 10;\n}\n.inline-container {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-top: 30px;\n  margin-bottom: 40px;\n}\n.inline-container label {\n  margin: 0;\n  /* Réinitialiser les marges du label */\n}\n.inline-container input[type=checkbox] {\n  margin: 65%;\n  /* Réinitialiser les marges de la case à cocher */\n  order: 2;\n  /* Réorganiser l'ordre pour placer la case à cocher à droite */\n}\n/*# sourceMappingURL=data:application/json;base64, */";